import React from 'react';
import './Rank.scss';

const Rank = (props) => {
  const { data } = props;
  return (
    <div className="rankInfo">
      <div className="userRank">
        <h1>  </h1>
      </div>
      <div className="userBadge">
        <img className="badgeImg" src={data?.badgeImageUrl} alt="" />
      </div>
    </div>
  );
};

export default Rank;
