import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './SignUp.scss';
import { BASE_AWS_API } from '../../config';

const SignUp = () => {
  const [userInfo, setUserInfo] = useState({
    nickname: '',
    height: 0,
    weight: 0,
    skeletalMuscleMass: 0,
    goalWeight: 0,
    bodyFat: 0,
    birthDate: '',
    gender: 'male',
    isSubscribe: '',
    badge: '',
  });
  const [errors, setErrors] = useState({});
  const [isAbledNickname, setIsAbledNickname] = useState(false);

  const handleSignUp = () => {
    const accessToken = localStorage.getItem('newUser');
    localStorage.removeItem('newUser');
    fetch(`${BASE_AWS_API}/auth/signup`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json;charset=utf-8',
        Authorization: accessToken,
      },
      body: JSON.stringify(userInfo),
    })
      .then((res) => res.json())
      .then((result) => {
        if (result.message === 'MODIFIED_SUCCESS') {
          localStorage.setItem('accessToken', accessToken);
          navigate('/');
        } else if (userInfo.nickname.length > 8) {
          alert(' 8   .');
        } else if (userInfo.height > 300) {
          alert(' 300  .');
        } else if (userInfo.age > 120) {
          alert('120   .');
        } else if (userInfo.skeletalMuscleMass > 100) {
          alert(' 100  .');
        } else if (userInfo.goalWeight > 500) {
          alert('  500  .');
        } else if (userInfo.weight > 500) {
          alert(' 500  .');
        } else if (userInfo.bodyFat > 100) {
          alert(' 100 .');
        } else if (isAbledNickname === false) {
          alert('   .');
        } else {
          alert('.  .');
        }
      });
  };

  const handleDoubleCheck = () => {
    const accessToken = localStorage.getItem('newUser');
    fetch(`${BASE_AWS_API}/users/nickname`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json;charset=utf-8',
        Authorization: accessToken,
      },
      body: JSON.stringify({ nickname: userInfo.nickname }),
    })
      .then((res) => res.json())
      .then((result) => {
        if (result.message === 'AVAILABLE_NICKNAME') {
          setIsAbledNickname(true);
          alert('   .');
        } else {
          setIsAbledNickname(false);
          alert('  .');
        }
      });
  };

  useEffect(() => {
    return () => {
      localStorage.removeItem('newUser');
    };
  }, []);

  const navigate = useNavigate();

  const saveJoinUserInfo = (event) => {
    const { name, value, type } = event.target;

    if (type === 'number') {
      event.target.value = value.replace(/[^0-9]/g, '');
    }
    setUserInfo({ ...userInfo, [name]: value });
    validateField(name, value);
  };
  const validateField = (fieldName, value) => {
    const fieldErrors = {};
    switch (fieldName) {
      case 'nickname':
        if (1 > value.length) {
          fieldErrors.nickname = '  .';
        } else if (value.length > 8) {
          fieldErrors.nickname = ' 8   .';
        } else {
          fieldErrors.nickname = '';
        }
        break;
      case 'age':
        if (1 > value) {
          fieldErrors.age = '   .';
        } else if (value > 120) {
          fieldErrors.age = '120   .';
        } else if (value.length > 3) {
          fieldErrors.age = '    .';
        } else {
          fieldErrors.age = '';
        }
        break;
      case 'height':
        if (1 > value) {
          fieldErrors.height = ' .';
        } else if (value > 300) {
          fieldErrors.height = '300  .';
        } else {
          fieldErrors.height = '';
        }
        break;
      case 'skeletalMuscleMass':
        if (1 > value) {
          fieldErrors.skeletalMuscleMass = ' .';
        } else if (value > 100)
          fieldErrors.skeletalMuscleMass =
            ' 100  .';
        else {
          fieldErrors.skeletalMuscleMass = '';
        }
        break;
      case 'goalWeight':
        if (1 > value) {
          fieldErrors.goalWeight = '  .';
        } else if (value > 500)
          fieldErrors.goalWeight = '  500  .';
        else {
          fieldErrors.goalWeight = '';
        }
        break;
      case 'weight':
        if (1 > value) {
          fieldErrors.weight = ' ';
        } else if (value > 500)
          fieldErrors.weight = ' 500  .';
        else {
          fieldErrors.weight = '';
        }
        break;
      case 'bodyFat':
        if (1 > value) {
          fieldErrors.bodyFat = ' .';
        } else if (value > 100)
          fieldErrors.bodyFat = ' 100 .';
        else {
          fieldErrors.goalWeight = '';
        }
        break;
      default:
        break;
    }
    setErrors({ ...errors, ...fieldErrors });
    console.log(isAbledNickname);
  };
  return (
    <div className="SignUp flexCenter">
      <div className="signUpInner ">
        <div className="logo">
          <img
            src="https://leechengwon.github.io/weet/images/logo2.png"
            alt=" "
          />
        </div>
        <h1>  </h1>
        <p>
             <b className="fontBold">  </b>
           .
        </p>
        <div
          className="inputWrapper"
          onInput={(e) => saveJoinUserInfo(e)}
          onWheel={(e) => e.target.blur()}
        >
          <label></label>
          <div className="nickNameInput inputBox">
            <input
              className="nickName"
              type="text"
              placeholder="ex)"
              name="nickname"
              disabled={isAbledNickname}
            />
            <button onClick={handleDoubleCheck}></button>
            {errors.nickname && <div className="errors">{errors.nickname}</div>}
          </div>

          <div className="genderAndageBox">
            <div className="ageInputWrapper inputBox">
              <span></span>
              <input
                className="age"
                type="number"
                name="age"
                placeholder="ex)29"
              />
              {errors.age && <div className="errors">{errors.age}</div>}
              <span></span>
            </div>

            <div className="genderCheck">
              <input
                className="male checkBoxInput"
                type="radio"
                name="gender"
                value="male"
              />
              <label></label>
              <input
                className="female checkBoxInput"
                type="radio"
                name="gender"
                value="female"
              />
              <label></label>
            </div>
          </div>

          <div className="inputBox">
            <label> (cm)</label>
            <input
              className="height"
              type="number"
              placeholder="ex)180"
              name="height"
            />
            {errors.height && <div className="errors">{errors.height}</div>}
          </div>

          <div className="inputBox">
            <label> (kg)</label>
            <input
              className="weight"
              type="number"
              placeholder="ex)70"
              name="weight"
            />
            {errors.weight && <div className="errors">{errors.weight}</div>}
          </div>

          <div className="inputBox">
            <label> (%)</label>
            <input
              className="fatPercentage"
              type="number"
              placeholder="ex)18"
              name="bodyFat"
            />
            {errors.bodyFat && <div className="errors">{errors.bodyFat}</div>}
          </div>

          <div className="inputBox">
            <label> (kg)</label>
            <input
              className="skeletalMuscle"
              type="number"
              placeholder="ex)35"
              name="skeletalMuscleMass"
            />
            {errors.skeletalMuscleMass && (
              <div className="errors">{errors.skeletalMuscleMass}</div>
            )}
          </div>

          <div className="inputBox">
            <label> (kg)</label>
            <input
              className="goalWeight"
              type="number"
              placeholder="ex)60"
              name="goalWeight"
            />
            {errors.goalWeight && (
              <div className="errors">{errors.goalWeight}</div>
            )}
          </div>
        </div>
        <button className="btnSignUp" onClick={handleSignUp}>
          
        </button>
      </div>
    </div>
  );
};

export default SignUp;
