import React from 'react';
import './Current.scss';
import Bar from '../../../../components/Bar/Bar';

const Current = (props) => {
  const { data, total } = props;
  return (
    <div className="current">
      <h1 className="ing"> </h1>
      <Bar
        label="  Level"
        unit="Level"
        total={total.badgeLevel}
        value={data?.badgeLevel}
      />

      <Bar label="" unit="cm" total={total.height} value={data?.height} />

      <Bar
        label=""
        unit="kg"
        total={total.weight}
        value={data?.weight}
      />

      <Bar
        label=""
        unit="kg"
        total={total.skeletalMuscleMass}
        value={data?.skeletalMuscleMass}
      />

      <Bar
        label=""
        unit="%"
        total={total.bodyFat}
        value={data?.bodyFat}
      />
    </div>
  );
};

export default Current;
