import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
// import { BASE_AWS_API } from '../../../../../config';
import './Comment.scss';

const Comment = ({ feedId, getCommentList }) => {
  const TOKEN = 'token';

  const navigate = useNavigate();
  const [comment, setComment] = useState('');

  const isCheckComment = comment.length >= 1;

  const handleComment = (event) => {
    setComment(event.target.value);
  };

  const handleCommentPost = (e) => {
    e.preventDefault();

    if (TOKEN) {
      if (isCheckComment) {
        fetch(``, {
          // fetch(`${BASE_AWS_API}/comments`, {
          // fetch(`http://localhost:8000/comments`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            Authorization: TOKEN,
          },
          body: JSON.stringify({
            feedId,
            content: comment,
          }),
        })
          .then((response) => {
            if (response.ok) {
              setComment('');
              getCommentList();
            }

            return response.json();
          })
          .then((result) => {
            if (result.message === 'COMMENT_ERROR') {
              alert(' 50   !');
            }
          });
      } else {
        alert(' .');
      }
    } else {
      alert('    .');
      navigate('/login');
    }
  };

  return (
    <form className="comment" onSubmit={handleCommentPost}>
      <input
        className="commentInput"
        name="commentInput"
        type="text"
        maxLength="50"
        placeholder=" ."
        onChange={handleComment}
        value={comment}
      />
      <button
        className="commentWriteBtn"
        type="submit"
        disabled={!isCheckComment}
      >
         
      </button>
    </form>
  );
};

export default Comment;
