import React from 'react';
import './Goal.scss';
import Bar from '../../../../components/Bar/Bar';

const Goal = (props) => {
  const { data, total } = props;
  return (
    <div className="goal">
      <h1 className="myGoal"> </h1>
      <Bar
        label=""
        unit="kg"
        total={total.goalWeight}
        value={data?.goalWeight}
      />

      <Bar
        label=""
        unit={data?.goalSkeletalMuscleMass === null ? '' : 'kg'}
        total={total.goalSkeletalMuscleMass}
        value={
          data?.goalSkeletalMuscleMass === null
            ? '   .'
            : data?.goalSkeletalMuscleMass
        }
      />

      <Bar
        label=""
        unit={data?.goalBodyFat === null ? '' : '%'}
        total={total.goalBodyFat}
        value={
          data?.goalBodyFat === null
            ? '   .'
            : data?.goalBodyFat
        }
      />
    </div>
  );
};

export default Goal;
