import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './Main.scss';
import Counter from '../../components/Counter/Counter';
import { BASE_AWS_API } from '../../config';
import Popup from '../../components/Popup/Popup';

const Main = () => {
  const [statisticsData, setStatisticsData] = useState({});
  const [popup, setPopup] = useState({});
  const navigate = useNavigate();
  const token = 'token';

  const closePopup = () => {
    setPopup({ ...popup, open: false });
  };

  const goToCommunity = () => {
    navigate('/community');
  };

  const goLogin = () => {
    navigate('login');
  };

  const goToExercise = () => {
    if (!token) {
      setPopup({
        open: true,
        title: '  .',
        leftBtnValue: ' ',
        rightBtnValue: '',
        leftBtnClick: goLogin,
        rightBtnClick: closePopup,
      });
    } else {
      navigate('exercise');
    }
  };

  const goToSubscribe = () => {
    if (!token) {
      setPopup({
        open: true,
        title: '  .',
        leftBtnValue: ' ',
        rightBtnValue: '',
        leftBtnClick: goLogin,
        rightBtnClick: closePopup,
      });
    } else {
      navigate('subscribe');
    }
  };

  useEffect(() => {
    fetch('../weet/data/main.json', {
      // fetch(`${BASE_AWS_API}/statistics`, {
      //   method: 'GET',
      //   headers: {
      //     'Content-Type': 'application/json;charset=utf-8',
      //     Authorization: localStorage.getItem('accessToken'),
      //   },
    })
      .then((res) => res.json())
      .then((result) => {
        if (result.message === 'READ_SUCCESS') {
          setStatisticsData(result.data);
        } else {
          alert('.  .');
        }
      });
  }, []);
  if (!statisticsData) {
    return null;
  }
  return (
    <div className="Main">
      <div className="mainBanner">
        <img
          src="https://leechengwon.github.io/weet/images/main-banner.png"
          alt=" "
        />
        <button className="btnMain" onClick={goToSubscribe}>
           
        </button>
      </div>
      <div className="sectionInner">
        <h2 className="bannerTit">    </h2>
        <div className="contentBannerWrapper">
          <div className="dietBanner boxBanner">
            <img
              src="https://leechengwon.github.io/weet/images/diet-banner.png"
              alt="  "
            />
            <div className="bannerDescription">
              <h2 className="descriptionTit"> </h2>
                 
              <br /> , , 
              <br />
               
              <br />    .
              <button className="btnBanner" onClick={goToExercise}>
                 
              </button>
            </div>
          </div>
          <div className="dietBanner boxBanner">
            <img
              src="https://leechengwon.github.io/weet/images/training-banner.png"
              alt="  "
            />
            <div className="bannerDescription">
              <h2 className="descriptionTit"> </h2>
               ,    <br />
                  .     ,{' '}
              <br />
                 .
              <button className="btnBanner" onClick={goToCommunity}>
                 
              </button>
            </div>
          </div>
          <div className="dietBanner boxBanner">
            <img
              src="https://leechengwon.github.io/weet/images/banner.png"
              alt=" "
            />
            <div className="bannerDescription">
              <h2 className="descriptionTit"> </h2>
               
              <br />
              ''  &nbsp;
              <br />   1:1  <br />
                .
              <button className="btnBanner" onClick={goToSubscribe}>
                 
              </button>
            </div>
          </div>
        </div>
      </div>
      <div className="challengeBannerWrapper">
        <div className="sectionInner">
          <h2 className="bannerTit">   </h2>
          <div className="challengeBanner">
            <img
              src="https://leechengwon.github.io/weet/images/c-banner.png"
              alt=""
            />
          </div>
        </div>
      </div>
      <div className="currentBannerWrapper">
        <h2 className="bannerTit">
          <div className="sectionInner"> </div>
        </h2>
        <div className="currentBanner">
          <div className="sectionInner">
            <div className="userCount countBox">
              <h3 className="descriptionTit">  </h3>
              <Counter
                initialValue={0}
                targetValue={statisticsData.userCount}
                unit=""
              />
            </div>
            <div className="feedCount countBox">
              <h3 className="descriptionTit">  </h3>
              <Counter
                initialValue={0}
                targetValue={statisticsData.feedCount}
                unit=""
              />
            </div>
            <div className="subscriberCount countBox">
              <h3 className="descriptionTit">  </h3>
              <Counter
                initialValue={0}
                targetValue={statisticsData.subscriberCount}
                unit=""
              />
            </div>
          </div>
        </div>
      </div>
      {popup.open && (
        <Popup
          title={popup.title}
          leftBtnValue={popup.leftBtnValue}
          rightBtnValue={popup.rightBtnValue}
          leftBtnClick={popup.leftBtnClick}
          rightBtnClick={popup.rightBtnClick}
        />
      )}
    </div>
  );
};

export default Main;
