import React, { useEffect, useState } from 'react';
import './Training.scss';
import TrainingContainer from '../../components/TrainingContainer/TrainingContainer';
// import { BASE_AWS_API } from '../../config';

const Training = () => {
  const [trainingData, setTrainingData] = useState({});
  const [currentWeight, setCurrentWeight] = useState();
  const [targetWeight, setTargetWeight] = useState();
  const shoulderIcon =
    'https://leechengwon.github.io/weet/images/icon-shoulder.png';
  const backIcon = 'https://leechengwon.github.io/weet/images/icon-back.png';
  const lowerBodyIcon =
    'https://leechengwon.github.io/weet/images/icon-lower.png';
  const chestIcon = 'https://leechengwon.github.io/weet/images/icon-chest.png';
  const armIcon = 'https://leechengwon.github.io/weet/images/icon-arm.png';

  function calculateCompletionRate(currentWeight, targetWeight) {
    if (currentWeight <= targetWeight) {
      return 100;
    } else {
      return ((targetWeight / currentWeight) * 100).toFixed(1);
    }
  }

  const completionRate = calculateCompletionRate(currentWeight, targetWeight);
  const remainingWeight = currentWeight - targetWeight;

  useEffect(() => {
    fetch('../weet/data/training.json', {
      // fetch(`${BASE_AWS_API}/training`, {
      //   method: 'GET',
      //   headers: {
      //     'Content-Type': 'application/json;charset=utf-8',
      //     Authorization: localStorage.getItem('accessToken'),
      //   },
    })
      .then((res) => res.json())
      .then((result) => {
        if (result.message === 'READ_SUCCESS') {
          setTrainingData(result.data);
          setCurrentWeight(result.data.currentWeight);
          setTargetWeight(result.data.targetWeight);
        } else {
          alert('.  .');
        }
      });
  }, []);
  if (!trainingData) {
    return null;
  }
  return (
    <div className="Training">
      <section className="sectionInner">
        <h2 className="pageLogo">
          <img
            src="https://leechengwon.github.io/weet/images/icon-exercise.png"
            alt=" "
          />
        </h2>
        <h1 className="pageText">  </h1>
        <p className="subText">
          ''       .
        </p>
        <p className="subNoticeText">
          *       30 .
        </p>
        <div className="userTrainingInfo">
          <div className="userNotice">
            <p className="badge">
              <img
                src="https://leechengwon.github.io/weet/images/icon-fat.png"
                alt=" "
              />
            </p>
            <span className="userNickname">{trainingData.nickName}</span>
            &nbsp;&nbsp;
            <span className="userCal"> {remainingWeight}kg </span>
            &nbsp;.
          </div>
          <div className="userWeightBarWrapper">
            <span className="weight"> {trainingData.startWeight}kg</span>
            <div className="targetWeightBar">
              <p className="currentBar" style={{ width: `${completionRate}%` }}>
                 {currentWeight}kg
              </p>
            </div>
            <span className="target"> {trainingData.targetWeight}kg</span>
          </div>
        </div>

        <div className="TrainingContentWrapper">
          <div className="trainingContainer">
            <h1 className="todayText">
              <p className="todayDate">2023.10.18</p>
              &nbsp; &nbsp; {trainingData.nickName}   &nbsp;
              &nbsp;
            </h1>
            <TrainingContainer
              trainingData={trainingData.shoulder}
              iconImg={shoulderIcon}
              exerciseArea=" "
            />
            <TrainingContainer
              trainingData={trainingData.back}
              iconImg={backIcon}
              exerciseArea=" "
            />
            <TrainingContainer
              trainingData={trainingData.lowerBody}
              iconImg={lowerBodyIcon}
              exerciseArea=" "
            />
            <TrainingContainer
              trainingData={trainingData.chest}
              iconImg={chestIcon}
              exerciseArea=" "
            />
            <TrainingContainer
              trainingData={trainingData.arm}
              iconImg={armIcon}
              exerciseArea=" "
            />
          </div>
        </div>
      </section>
    </div>
  );
};

export default Training;
