import React, { useEffect, useRef } from 'react';
import Feed from './Feed/Feed';
import './FeedList.scss';

const FeedList = ({
  feedList,
  setFeedList,
  totalCount,
  page,
  limit,
  setPaginationParams,
  fetchFeedList,
}) => {
  const listRef = useRef(null);

  //     
  const itemsPerPage = 10;

  //   
  const lastPage = Math.ceil(totalCount / itemsPerPage);

  //  
  const currentPage = parseInt(page) || 1;

  useEffect(() => {
    const { current } = listRef;

    if (!current) return;

    const handleScroll = () => {
      //       
      if (currentPage >= lastPage) {
        return;
      }

      if (
        current.scrollTop + current.offsetHeight + 2 > current.scrollHeight &&
        feedList.feeds.length < totalCount &&
        feedList.feeds.length >= page * limit
      ) {
        setPaginationParams();
      }
    };

    current.addEventListener('scroll', handleScroll);

    return () => {
      current.removeEventListener('scroll', handleScroll);
    };
  }, [feedList, totalCount, page]);

  const removeFeed = (feed) => {
    const index = feedList.feeds.indexOf(feed);
    setFeedList(feedList.feeds.splice(index, 1));
  };

  return (
    <ul ref={listRef} className="feedList">
      {feedList.feeds?.map((feed) => (
        <Feed
          key={feed.id}
          getFeed={fetchFeedList}
          data={feed}
          removeFeed={removeFeed}
        />
      ))}
    </ul>
  );
};

export default FeedList;
